	module.exports = {"data":{"markdownRemark":{"html":"<h2>Font Size Attribute</h2>\n<p>This attribute specifies the font size as either a numeric or relative value. Numeric values range from <code>1</code> to <code>7</code> with <code>1</code> being the smallest and <code>3</code> the default. It can also be defined using a relative value, like <code>+2</code> or <code>-3</code>, which set it relative to the value of the size attribute of the <code>&#x3C;basefont></code> element, or relative to <code>3</code>, the default value, if none does exist.</p>\n<p>Syntax: </p>\n<p><code>&#x3C;font size=\"number\"></code></p>\n<p>Example:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-html\"><code><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>html</span><span class=\"token punctuation\">></span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>body</span><span class=\"token punctuation\">></span></span>\n    <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>font</span> <span class=\"token attr-name\">size</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>6<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span>This is some text!<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>font</span><span class=\"token punctuation\">></span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>body</span><span class=\"token punctuation\">></span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>html</span><span class=\"token punctuation\">></span></span>\n</code></pre>\n      </div>\n<p>Note : <code>The size attribute of &#x3C;font> is not supported in HTML5. Use CSS instead.</code></p>","fields":{"slug":"/html/attributes/font-size-attribute/"},"frontmatter":{"title":"Font Size Attribute"}}},"pathContext":{"slug":"/html/attributes/font-size-attribute/"}}

